@include _920 {
	#menu {
		position: fixed;
		top: -100%;
		left: 0;
		overflow-x: hidden;
		overflow-y: auto;
		min-width: 100%;
		height: 0;
		min-height: 100%;
		transition: all 0.3s ease-in-out;
		opacity: 0;
		background: #fff;
		background: linear-gradient(to right, #fff 20%, #80f0ff, #019eb3);
	}

	#menu._overlay {
		position: fixed;
		z-index: 2;
		top: 0;
		display: block;
		visibility: visible;
		min-width: 100%;
		min-height: 100%;
		transition: all 0.3s ease-in-out;
		opacity: 1;
		color: #00545d;
		background: #fff;
		background: linear-gradient(to right, #fff 20%, #80f0ff, #019eb3);
	}
}
